import React from "react";
import "./Footer.css";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import FacebookIcon from "@material-ui/icons/Facebook";
import TwitterIcon from "@material-ui/icons/Twitter";
import InstagramIcon from "@material-ui/icons/Instagram";
import { HashLink as Link } from "react-router-hash-link";
import "animate.css/animate.min.css";

function Footer() {
  return (
    <div className="footer">
      <Row className="footer-firstRow">
        <Col md={3} xs={6} className="footer-first-from-leftSide">
          <h1 className="footerLogo">ITPEAKS</h1>
          <h6>"ITPEAKS provides solutions for all of your Technology needs"</h6>
          <div className="socialMedia-div">
            <div className="icon-inner-div">
              <a
                className="icon-link"
                href="https://www.facebook.com/ITPeaks-108659560813563"
              >
                <FacebookIcon />
              </a>
            </div>
            <div className="icon-inner-div">
              <a className="icon-link" href="https://twitter.com/IT_Peaks">
                <TwitterIcon />
              </a>
            </div>
            <div className="icon-inner-div">
              <a
                className="icon-link"
                href="https://www.instagram.com/itpeaks/"
              >
                <InstagramIcon />
              </a>
            </div>
          </div>
        </Col>
        <Col md={3} xs={6} className="footer-firstRow-col1-2  f-f-cols-1">
          <h5 className="footer-topLink">Who We Are</h5>
          <span className="footer-topLink-underline"></span>

          <div className="f-f-cols-1-links-div">
            <Link to="about#our-capabilities" className="footer-Link">
              Our Capabilities
            </Link>

            <Link to="about#how-we-work" className="footer-Link">
              How we work
            </Link>

            <Link to="about#leadership" className="footer-Link">
              Our Team
            </Link>
          </div>
        </Col>
        <Col md={3} xs={6} className="footer-firstRow-col1-1 f-f-cols-1">
          <h5 className="footer-topLink">What We Do</h5>
          <span className="footer-topLink-underline"></span>

          <div className="f-f-cols-1-links-div">
            <h6>Services</h6>
            <Link to="services#branding" className="footer-Link">
              Branding
            </Link>

            <Link to="services#mobile-development" className="footer-Link">
              Mobile Development
            </Link>

            <Link to="services#web-development" className="footer-Link">
              Web Development
            </Link>

            <Link to="services#system-integration" className="footer-Link">
              System integration
            </Link>
            <br />
          </div>
        </Col>

        <Col md={3} xs={6} className="footer-firstRow-col1-3  f-f-cols-1">
          <h5 className="footer-topLink">Where To Find Us</h5>
          <span className="footer-topLink-underline"></span>
          <div className="f-f-cols-1-links-div">
            <h6>Contact Us</h6>
            <a>+20 1555280369</a>
            <a>ITPeaks@gmail.com</a>
            <br />
            <h6>Our Locaction</h6>
            <a>1786, Drievea , California</a>
          </div>
        </Col>
      </Row>
      <Row className="footer-secondRow">
        <div className="footer-copyright text-center py-3">
          &copy; {new Date().getFullYear()} Copyright:
          <a href="/" className="a-copyrights">
            <span>{"    "}</span> ITPeaks. All rights reserved.
          </a>
        </div>
      </Row>
    </div>
  );
}

export default Footer;
