import React from "react";
import NavBar from "./NavBar.js";
import "./ServicesPage.css";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { Link } from "react-router-dom";
import "animate.css/animate.min.css";
import ScrollAnimation from "react-animate-on-scroll";
import Button from "@material-ui/core/Button";
import branding from "../img/branding-icon3.png";
import mobileDevelopment from "../img/mobile-development-icon3.png";
import webDevelopment from "../img/web-development-icon3.1png.png";
import systemIntegrating from "../img/system-integrating-icon4.png";
import Image from "react-bootstrap/Image";
function ServicesPage() {
  return (
    <div className="services-page">
      <NavBar />
      <Row className="navBar-row-space"></Row>
      <div className="services-page-content-div">
        <Row className="services-fp-mainRow">
          <Col md={5} xs={12} className="services-fp-firstPart">
            <div className="services-fp-firstPart-content-div">
              <h1 className="services-fp-h1-title">
                Services We <br />
                Offer
              </h1>
              <p className="services-fp-para-text">
                Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                Consectetur voluptas impedit, ratione velit esse suscipit?
                Eveniet reprehenderit facilis officia, consequuntur rerum
                exercitationem
              </p>
            </div>
            <Button variant="contained" className="services-fp-button">
              <Link to="/contact" className="tele-link-nav">
                Lets's Work Together
              </Link>
            </Button>
          </Col>
        </Row>
        <Row className="secondRow3">
          <Col className="services-index-cols" md={3} xs={3}>
            <ScrollAnimation
              animateIn="animate__fadeInLeft"
              animateOut="animate__fadeOut"
              duration={1}
            >
              <div className="services-card2" id="services-card21">
                <Image
                  src={branding}
                  width="130"
                  height="130"
                  className="services-page-image-icon"
                />
                <a href="#branding" className="index-link">
                  <h5 className="cardTitle2" id="cardTitle21">
                    Branding
                  </h5>
                </a>
              </div>
            </ScrollAnimation>
          </Col>
          <Col className="services-index-cols" md={3} xs={3}>
            <ScrollAnimation
              animateIn="animate__fadeIn"
              animateOut="animate__fadeOut"
              duration={1}
            >
              <div className="services-card2" id="services-card22">
                <Image
                  src={mobileDevelopment}
                  width="130"
                  height="130"
                  className="services-page-image-icon"
                />
                <a href="#mobile-development" className="index-link">
                  <h5 className="cardTitle2" id="cardTitle22">
                    Mobile Development
                  </h5>
                </a>
              </div>
            </ScrollAnimation>
          </Col>
          <Col className="services-index-cols" md={3} xs={3}>
            <ScrollAnimation
              animateIn="animate__fadeIn"
              animateOut="animate__fadeOut"
              duration={1}
            >
              <div className="services-card2" id="services-card23">
                <Image
                  src={webDevelopment}
                  width="130"
                  height="130"
                  className="services-page-image-icon"
                />
                <a href="#web-development" className="index-link">
                  <h5 className="cardTitle2" id="cardTitle23">
                    Web Development
                  </h5>
                </a>
              </div>
            </ScrollAnimation>
          </Col>
          <Col className="services-index-cols" md={3} xs={3}>
            <ScrollAnimation
              animateIn="animate__fadeInRight"
              animateOut="animate__fadeOut"
              duration={1}
            >
              <div className="services-card2" id="services-card24">
                <Image
                  src={systemIntegrating}
                  width="130"
                  height="130"
                  className="services-page-image-icon"
                />
                <a href="#system-integration" className="index-link">
                  <h5 className="cardTitle2" id="cardTitle24">
                    System Integration
                  </h5>
                </a>
              </div>
            </ScrollAnimation>
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default ServicesPage;
