import React from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Home from "./components/Home.js";
import Footer from "./components/Footer.js";
import Employment from "./components/Employment.js";
import ServicesPage from "./components/ServicesPage.js";
import ServicesPagePlus from "./components/ServicesPagePlus.js";
import JoinUs from "./components/JoinUs.js";
import About from "./components/About.js";
import Contact from "./components/Contact.js";
import Container from "@material-ui/core/Container";
import CssBaseline from "@material-ui/core/CssBaseline";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import ScrollToTop from "./components/ScrollToTop";
import "./components/normalizeCss.css";
import ServicesMini from "./components/ServicesMini.js";
import AboutMini from "./components/AboutMini.js";
import NavBar from "./components/NavBar.js";
import Blog from "./components/Blog.js";
import ItTeam from "./components/ItTeam.js";

function App() {
  return (
    <Router>
      <div className="App">
        <ScrollToTop />
        <Switch>
          <Route path="/team">
            <CssBaseline>
              <ItTeam />
              <Footer />
            </CssBaseline>
          </Route>
          <Route path="/blog">
            <CssBaseline>
              <Blog />
              <Footer />
            </CssBaseline>
          </Route>
          <Route path="/joinOurTeam">
            <CssBaseline>
              <JoinUs />
              <Footer />
            </CssBaseline>
          </Route>
          <Route path="/contact">
            <CssBaseline>
              <Contact />
              <Footer />
            </CssBaseline>
          </Route>
          <Route path="/about">
            <CssBaseline>
              <About />
              <Footer />
            </CssBaseline>
          </Route>
          <Route path="/services">
            <CssBaseline>
              <ServicesPage />
              <ServicesPagePlus />
              <Footer />
            </CssBaseline>
          </Route>
          <Route path="/">
            <CssBaseline>
              <NavBar />
              <Home />
              <AboutMini />
              <ServicesMini />
              <Employment></Employment>
              <Footer></Footer>
            </CssBaseline>
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
