import React from "react";
import "./AboutMini.css";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "animate.css/animate.min.css";
import ScrollAnimation from "react-animate-on-scroll";
import { Link } from "react-router-dom";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import howWeWork from "../img/howWeWork-pic.jpg";
import Image from "react-bootstrap/Image";
function AboutMini() {
  return (
    <div className="about-mini">
      <Row className="about-us-mini-heading-container">
        <h1 className="about-us-mini-heading-title">
          About <span className="about-us-span-us">Us</span>
        </h1>
        <p className="about-us-mini-heading-para">
          ITPEAKS provides solutions for all of your Technology needs; We
          support you in any IT endeavor and ensure you cross the finish line in
          record time. We help you reach the top.
        </p>
      </Row>
      <Row className="about-us-mini-main-row">
        <Col md={3} xs={12} className="about-us-howWeWork-col1">
          <div className="about-us-image-background"></div>
          <div className="about-us-howWeWork-content1">
            <h1 className="about-us-howWeWork-content-h1">Our Capabilities</h1>
            <p className="about-us-howWeWork-content-p">
              We got a unqiue capabilities that stand out among others.
            </p>
          </div>
        </Col>
        <Col md={3} xs={12} className="about-us-howWeWork-col2">
          <div className="about-us-image-background"></div>
          <div className="about-us-howWeWork-content2">
            <h1 className="about-us-howWeWork-content-h1">How We Work</h1>
            <p className="about-us-howWeWork-content-p">
              Our aim is to help you deliver great website experiences, increase
              revenues and grow your business.
            </p>
          </div>
        </Col>
        <Col md={3} xs={12} className="about-us-howWeWork-col3">
          <div className="about-us-image-background"></div>
          <div className="about-us-howWeWork-content3">
            <h1 className="about-us-howWeWork-content-h1">Our Leadership</h1>
            <p className="about-us-howWeWork-content-p">
              Checkout our perfect team and how we run this business.
            </p>
          </div>
        </Col>
      </Row>
      <Row className="about-us-mini-button-container">
        <Button variant="contained" className="about-us-fp-button">
          <Link to="/about" className="tele-link-nav">
            Learn More
          </Link>
        </Button>
      </Row>
    </div>
  );
}

export default AboutMini;
